/*
ajax 
*/
/**
 *  ajax
 * @param {*} url 
 * @param {*} data 
 * @param {*} type POST
 * 
 */


export default function ajax(url = '', data = {}, type = 'POST') {
	let baseUrl = uni.getStorageSync('yuming');
	//  Promise response.dataresponse
	if (type === 'GET') {
		//  url query 
		let dataStr = '' // dataurl
		Object.keys(data).forEach(key => {
			dataStr += key + '=' + data[key] + '&'
		})
		if (dataStr !== '') {
			dataStr = dataStr.substring(0, dataStr.lastIndexOf('&'))
			url = url + '?' + dataStr
		}
		//  get 
		return new Promise(function(resolve, reject) {
			//  
			uni.request({
				url: baseUrl + url,
				method: type,
				header: {
					'Content-Type': 'application/x-www-form-urlencoded',
					'charset': 'utf-8',
					'X-Requested-With': 'XMLhttpRequest'
				},

				success: res => {
					resolve(res)
				},
				fail: (err) => {
					console.log('errrr')
					uni.navigateTo({
						url: '/pages/login/login'
					})
					reject(err)
				}
			});
		})
	} else {
		return new Promise(function(resolve, reject) {
			//  
			uni.request({
				url: baseUrl + url,
				method: type,
				header: {
					'Content-Type': 'application/x-www-form-urlencoded',
					'charset': 'utf-8',
					'X-Requested-With': 'XMLhttpRequest'
				},
				data: data,
				success: res => {
					resolve(res)
				},
				fail: (err) => {
					console.log('errrr')
					uni.navigateTo({
						url: '/pages/login/login'
					})
					reject(err)
				}
			});
		})
	}

}
