/*
ajax 
*/
/**
 *  ajax
 * @param {*} url 
 * @param {*} data 
 * @param {*} type POST
 * 
 */

import {getYuMing} from '@/utils/auth.js'

export default function ajax(url = '', data = {}, type = 'POST') {
	let baseUrl = getYuMing();
	if(baseUrl){
		if(url.indexOf(baseUrl)===-1){
			url=baseUrl+url
		}	
	}
	if (type === 'GET') {
		//  url query 
		let dataStr = '' // dataurl
		Object.keys(data).forEach(key => {
			dataStr += key + '=' + data[key] + '&'
		})
		if (dataStr !== '') {
			dataStr = dataStr.substring(0, dataStr.lastIndexOf('&'))
			url = url + '?' + dataStr
		}
		//  get 
		return new Promise(function(resolve, reject) {
			//  
			uni.request({
				url:url,
				method: type,
				header: {
					'Content-Type': 'application/x-www-form-urlencoded',
					'charset': 'utf-8',
					'X-Requested-With': 'XMLhttpRequest'
				},

				success: res => {
					resHandle(res,resolve)
				},
				fail: (err) => {
					reject(err)
					uni.showToast({
						title: ',',
						duration: 2000
					});
				}
			});
		})
	} else {
		return new Promise(function(resolve, reject) {
			//  
			uni.request({
				url:url,
				method: type,
				header: {
					'Content-Type': 'application/x-www-form-urlencoded',
					'charset': 'utf-8',
					'X-Requested-With': 'XMLhttpRequest'
				},
				data: data,
				success: res => {
					resHandle(res,resolve)
				},
				fail: (err) => {	
					reject(err)
					uni.showToast({
						title: ',',
						icon:'none',
						duration: 2000
					});
				}
			});
		})
	}

}

function resHandle(res,resolve){
	if(res.data.code===0){
		resolve(res)
	}else if(res.data.code===1001){
		uni.showToast({
			title: res.data.msg,
			duration: 2000,
			icon:'none',
			success:()=>{
				uni.navigateTo({
					url: '/pages/login/login'
				})
			}
		});	
	}else if(res.data.code===1||res.data.code===1002){
			uni.showModal({
			content: res.data.msg,
			confirmText:'',
			confirmColor:'#3690ff',
			showCancel: false,	
			success: res => {
				// if (res.confirm) {
				// 	
				// } else if (res.cancel) {
				// 
				// }		
			}
		});
	
	}
}